.login_form {
  width: 350px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  gap: 8px;
  margin: 0 auto 500px;
}
.login_form > h2 {
  font-size: 24px;
  font-weight: bold;
  width: 100%;
  text-align: center;
}
.login_form > input {
  height: 40px;
}
.login_form > p:first-of-type {
  text-align: right;
  font-size: 12px;
}
.login_form > input[type=text] {
  border: 1px solid #ccc;
}
.login_form > input[type=button], .login_form > input[type=submit] {
  border: 1px solid rgb(95, 0, 128);
  font-weight: bold;
}
.login_form > input[type=submit] {
  background-color: rgb(95, 0, 128);
  color: #fff;
}
.login_form > input[type=button] {
  background-color: transparent;
  color: rgb(95, 0, 128);
}